// Colors
$white: #FFFFFF;
$black: #000000;
$primary-blue: #2563EB;
$light-gray: #F1F1F1;
$medium-gray: #A1A1A1;
$border-gray: #EAEAEA;
$border-light: #E1E1E1;
$border-medium: #E5E7EB;
$border-light-2: #ECECEC;
$border-light-3: #EDEDED;

// Text Colors
$text-primary: #09090B;
$text-secondary: #8EADC0;
$text-muted: #6B7280;
$text-light: #A0A4A8;
$text-disabled: #C5C6C8;
$text-light-gray: #374151;

// Icon Colors
$icon-gray: #9CA3AF;
$icon-light: #C4C4C4;
$icon-medium: #AAAAAA;
$icon-dark: #4B5563;

// Project Tag Colors
$ellaments-bg: #A5F2B7;
$ellaments-text: #057B14;
$project-a-bg: #B5CCFF;
$project-a-text: #1757C1;

// Fonts
$font-primary: 'Inter', sans-serif;
$font-secondary: 'Geist', sans-serif;

// Font Weights
$font-normal: 400;
$font-medium: 500;
$font-semibold: 600;

// Font Sizes
$font-size-xs: 8px;
$font-size-sm: 10px;
$font-size-base: 11px;
$font-size-md: 12px;
$font-size-lg: 14px;
$font-size-xl: 16px;

// Line Heights
$line-height-sm: 12px;
$line-height-base: 16px;
$line-height-md: 18px;
$line-height-lg: 20px;
$line-height-xl: 24px;

// Spacing
$spacing-xs: 2px;
$spacing-sm: 4px;
$spacing-md: 8px;
$spacing-lg: 12px;
$spacing-xl: 16px;
$spacing-2xl: 20px;
$spacing-3xl: 24px;

// Border Radius
$border-radius-sm: 2px;
$border-radius-base: 4px;
$border-radius-md: 6px;
$border-radius-lg: 8px;

// Layout
$sidebar-width: 354.99px;
$header-height: 51px;
$nav-height: 64px; 